.info-search-history-wrapper {
  width: 290px;

  .top-search-history {
    position: relative;

    display: grid;
    grid-template-columns: 260px 30px;
    grid-template-rows: auto;

    cursor: pointer;
    line-height: 40px;

    &:hover .btn-delete-search {
      display: block;
      animation: fadeIn ease-in 0.6s;

      @keyframes fadeIn {
        from {
          opacity: 0;
        }
        to {
          opacity: 1;
        }
      }
    }

    .content-search {
      margin-left: 10px;
    }

    .btn-delete-search {
      position: absolute;
      right: 16px;
      top: 0px;
      display: none;

      width: 12px;
      height: 11px;

      cursor: pointer;
    }
  }
}
